* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0a1929;
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  padding: 20px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Header */
.header {
  text-align: center;
  color: white;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-weight: 700;
  color: #66b2ff;
}

.header p {
  font-size: 1.1rem;
  color: #b0bec5;
}

/* Content */
.content {
  background: #132f4c;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  border: 1px solid #1e4976;
}

/* Input Section */
.input-section {
  padding: 40px;
  background: #1a3a52;
  border-bottom: 1px solid #2a5a7a;
}

.input-section h2 {
  color: #ffffff;
  margin-bottom: 30px;
  font-size: 1.8rem;
}

.spending-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.input-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.input-group label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  color: #b0bec5;
  font-size: 0.95rem;
}

.category-icon {
  font-size: 1.3rem;
}

.category-label {
  flex: 1;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.currency-symbol {
  position: absolute;
  left: 16px;
  color: #66b2ff;
  font-weight: 600;
  pointer-events: none;
  font-size: 1.1rem;
}

.input-wrapper input {
  width: 100%;
  padding: 14px 16px 14px 32px;
  border: 2px solid #2a5a7a;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #0a1929;
  color: #ffffff;
}

.input-wrapper input:focus {
  outline: none;
  border-color: #66b2ff;
  box-shadow: 0 0 0 3px rgba(102, 178, 255, 0.2);
}

.input-wrapper input::placeholder {
  color: #546e7a;
}

/* Buttons */
.button-group {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.btn {
  padding: 14px 32px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-primary {
  background: linear-gradient(135deg, #66b2ff 0%, #2979ff 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 178, 255, 0.4);
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 178, 255, 0.5);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background: transparent;
  color: #66b2ff;
  border: 2px solid #66b2ff;
}

.btn-secondary:hover {
  background: rgba(102, 178, 255, 0.1);
  transform: translateY(-2px);
}

/* Error Message */
.error-message {
  background: rgba(244, 67, 54, 0.1);
  border: 2px solid #f44336;
  border-radius: 8px;
  padding: 12px 16px;
  color: #ff6b6b;
  margin-bottom: 20px;
  font-weight: 500;
}

/* Results Section */
.results-section {
  padding: 40px;
  background: #132f4c;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.summary-card {
  background: linear-gradient(135deg, #1e4976 0%, #2a5a7a 100%);
  color: white;
  border-radius: 12px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid #3a6a9a;
}

.summary-card h3 {
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: #66b2ff;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
}

.stat {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stat-label {
  font-size: 0.9rem;
  color: #b0bec5;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
}

/* Category Breakdown Summary */
.category-breakdown-summary {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.category-breakdown-summary h4 {
  color: #66b2ff;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

.category-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.category-chip {
  background: rgba(102, 178, 255, 0.15);
  border: 1px solid rgba(102, 178, 255, 0.3);
  border-radius: 20px;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.chip-label {
  color: #b0bec5;
  font-size: 0.9rem;
}

.chip-amount {
  color: #66b2ff;
  font-weight: 700;
}

/* Strategy Toggle */
.strategy-toggle {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  background: #1a3a52;
  padding: 8px;
  border-radius: 8px;
}

.toggle-btn {
  flex: 1;
  padding: 12px 24px;
  background: transparent;
  border: none;
  border-radius: 6px;
  color: #b0bec5;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-btn.active {
  background: #66b2ff;
  color: white;
  box-shadow: 0 2px 8px rgba(102, 178, 255, 0.3);
}

.toggle-btn:hover:not(.active) {
  background: rgba(102, 178, 255, 0.1);
}

/* Multi-Card Strategies */
.multi-card-section h2 {
  color: #66b2ff;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.section-subtitle {
  color: #b0bec5;
  margin-bottom: 30px;
}

.strategy-card {
  background: #1a3a52;
  border: 1px solid #2a5a7a;
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 20px;
}

.strategy-card h3 {
  color: #66b2ff;
  margin-bottom: 20px;
  font-size: 1.3rem;
}

.strategy-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
}

.strategy-stat {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.strategy-stat .label {
  color: #b0bec5;
  font-size: 0.9rem;
}

.strategy-stat .value {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
}

.strategy-stat.highlight .value {
  color: #4caf50;
}

.strategy-stat .value.negative {
  color: #ff6b6b;
}

.strategy-cards {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.strategy-card-item {
  background: #132f4c;
  border: 1px solid #2a5a7a;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 15px;
}

.card-badge {
  background: #66b2ff;
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 0.9rem;
}

.card-info h4 {
  color: #ffffff;
  margin-bottom: 8px;
}

.card-quick-stats {
  display: flex;
  gap: 20px;
  color: #b0bec5;
  font-size: 0.9rem;
}

/* Single Card Results */
.results-section > h2 {
  color: #66b2ff;
  margin-bottom: 10px;
  font-size: 1.8rem;
}

.results-subtitle {
  color: #b0bec5;
  margin-bottom: 30px;
  font-size: 1rem;
}

.cards-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 40px;
}

.card-item {
  background: #1a3a52;
  border: 2px solid #2a5a7a;
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.card-item:hover {
  border-color: #66b2ff;
  box-shadow: 0 8px 25px rgba(102, 178, 255, 0.2);
  transform: translateY(-2px);
}

.card-item.top-pick {
  border-color: #ffc107;
  background: linear-gradient(135deg, #1e4976 0%, #2a5a7a 100%);
  box-shadow: 0 8px 25px rgba(255, 193, 7, 0.3);
}

.best-badge {
  position: absolute;
  top: -2px;
  right: 30px;
  background: linear-gradient(135deg, #ffc107 0%, #ff9800 100%);
  color: #000;
  padding: 8px 20px;
  border-radius: 0 0 12px 12px;
  font-weight: 700;
  font-size: 0.85rem;
  box-shadow: 0 4px 10px rgba(255, 193, 7, 0.4);
  letter-spacing: 0.5px;
}

.card-header {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 20px;
}

.card-rank {
  background: #66b2ff;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  flex-shrink: 0;
}

.card-name-section {
  flex: 1;
}

.card-name {
  color: #ffffff;
  font-size: 1.4rem;
  margin-bottom: 5px;
}

.card-notes {
  color: #b0bec5;
  font-size: 0.9rem;
  font-style: italic;
}

.card-stats {
  background: #132f4c;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid #2a5a7a;
}

.stat-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.stat-row:last-child {
  border-bottom: none;
}

.stat-row.total {
  border-top: 2px solid #66b2ff;
  margin-top: 8px;
  padding-top: 16px;
  font-weight: 700;
}

.stat-row.highlight-row {
  background: rgba(102, 178, 255, 0.1);
  margin: -10px -10px 10px -10px;
  padding: 15px 10px;
  border-radius: 6px;
}

.stat-row .stat-label {
  color: #b0bec5;
  font-size: 0.95rem;
}

.stat-row .stat-value {
  font-weight: 600;
  font-size: 1.1rem;
  color: #ffffff;
}

.stat-row .stat-value.highlight {
  color: #4caf50;
  font-size: 1.3rem;
  font-weight: 700;
}

.stat-row .stat-value.positive {
  color: #4caf50;
}

.signup-bonus {
  background: rgba(76, 175, 80, 0.15);
  border-left: 4px solid #4caf50;
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  font-size: 0.9rem;
  color: #b0bec5;
  line-height: 1.5;
}

.signup-bonus strong {
  color: #4caf50;
}

.rewards-breakdown {
  margin-top: 20px;
}

.rewards-breakdown h4 {
  color: #66b2ff;
  margin-bottom: 12px;
  font-size: 1rem;
}

.breakdown-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
}

.breakdown-item {
  background: #132f4c;
  border: 1px solid #2a5a7a;
  border-radius: 6px;
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.breakdown-category {
  color: #b0bec5;
  font-size: 0.85rem;
  text-transform: capitalize;
}

.breakdown-amount {
  color: #4caf50;
  font-weight: 700;
  font-size: 1rem;
}

/* Info Box */
.info-box {
  background: rgba(30, 73, 118, 0.5);
  border-left: 4px solid #66b2ff;
  border-radius: 8px;
  padding: 20px;
  margin-top: 30px;
  border: 1px solid #2a5a7a;
}

.info-box h4 {
  color: #66b2ff;
  margin-bottom: 12px;
  font-size: 1.1rem;
}

.info-box ul {
  list-style: none;
  padding: 0;
}

.info-box li {
  color: #b0bec5;
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
  line-height: 1.6;
}

.info-box li:before {
  content: "â†’";
  position: absolute;
  left: 0;
  color: #66b2ff;
  font-weight: 700;
}

.info-box strong {
  color: #ffffff;
}

/* Footer */
.footer {
  text-align: center;
  padding: 30px 20px;
  color: #b0bec5;
}

.footer p {
  margin-bottom: 8px;
  font-size: 0.95rem;
}

.disclaimer {
  font-size: 0.8rem;
  opacity: 0.7;
  font-style: italic;
}

/* Responsive */
@media (max-width: 768px) {
  .header h1 {
    font-size: 2rem;
  }

  .input-section,
  .results-section {
    padding: 24px;
  }

  .spending-grid {
    grid-template-columns: 1fr;
  }

  .summary-stats {
    grid-template-columns: 1fr;
  }

  .stat-value {
    font-size: 1.5rem;
  }

  .card-name {
    font-size: 1.2rem;
  }

  .breakdown-grid {
    grid-template-columns: 1fr;
  }

  .button-group {
    flex-direction: column;
  }

  .btn {
    width: 100%;
  }

  .strategy-toggle {
    flex-direction: column;
  }

  .category-chips {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .app {
    padding: 10px;
  }

  .header h1 {
    font-size: 1.5rem;
  }

  .header p {
    font-size: 0.95rem;
  }

  .input-section h2,
  .results-section > h2 {
    font-size: 1.4rem;
  }
}